import React from 'react';
import bridge from '@vkontakte/vk-bridge';
import View from '@vkontakte/vkui/dist/components/View/View';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import { AdaptivityProvider, AppRoot } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import Textarea from '@vkontakte/vkui/dist/components/Textarea/Textarea';
import { FormItem } from '@vkontakte/vkui/dist/components/FormItem/FormItem';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import '@vkontakte/vkui/dist/vkui.css';
class App extends React.Component {

	constructor() {
		super();

		this.state = {
			text: ''
			
		};
		
		this.changeText = this.changeText.bind(this);
		this.getItems= this.getItems.bind(this);

	}

	changeText(e) {
		let text = e.target.value
		this.setState({ text: text });
	}
	getItems(text) {
		

		
		let api = `https://syumsh.org/gpt3/?text=${encodeURI(text)}`
		fetch(api)
		.then(data => data.text())
		.then((result) => {
			console.log(result)
			this.setState({text: result})
		})
		.catch(e => [])
	}
	render() {
		return(
			<View activePanel = "mainPanel" >

				<Panel id="mainPanel">
					<PanelHeader>GPT3</PanelHeader>
					<Group>
						<FormItem top="Введите текст">
							<Textarea id="UniDiArr" value={this.state.text} onChange={this.changeText} placeholder="Для торговли на бирже понадобится брокер. Брокер - ваш представитель и проводник." />
							<Div><Button onClick={() => this.getItems(this.state.text)} mode="commerce">Продолжить!</Button></Div>
						</FormItem>
					</Group>
				</Panel>
		</View>
		);
	}
}

export default App;

